.post{
    width: 385px;
    margin: 0px 25px 40px 25px;
}
.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 7px;
}
.postInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    
}
.postCat{
    font-family: 'Varela Round', sans-serif; 
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    color:#be9656;
    /*line-height: 20px;*/
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}
.postTitle{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}
.postAuthorDate{
    margin-bottom: 20px;
    font-style: italic;
    display: flex;
    justify-content: space-between;
    font-size: 13px;
    font-family: 'Varela Round', sans-serif;
    color: #b39656;
    margin-top: 10px;
}

.postDesc{
    font-family: 'Varela Round', sans-serif;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    margin-top: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}